<template>
  <div class="message">
    <div class="message-img">
      <img src="../assets/messageBoard.png" alt="message-img">
      <div class="message-font">ÁïôË®ÄÊùø</div>
    </div>
    <comment title="ÁïôË®Ä" :commentList="commentList" v-on:submitComment="addCommentFunc" v-on:deleteCommment="deleteCommmentFunc"></comment>
  </div>
</template>

<script>
import comment from '@/components/layout/comment'
export default {
  name: 'messageBoard',
  components: {
    comment
  },
  data () {
    return {
      commentList: [],
      url: {
        addCommentUrl: this.$wang.api.addCommentUrl,
        getCommentlistUrl: this.$wang.api.getCommentlistUrl,
        deleteCommentUrl: this.$wang.api.deleteCommentUrl
      }
    }
  },
  methods: {
    // Ëé∑ÂèñÁïôË®ÄÂàóË°®
    getCommentFunc () {
      this.$wang.ajax({
        url: this.url.getCommentlistUrl,
        param: {
          type: '1'
        }
      }).then(res => {
        this.commentList = res.data
      })
    },
    // Ê∑ªÂä†ÁïôË®ÄÂàóË°®
    addCommentFunc (data) {
      let param = {
        content: data,
        author: this.$store.state.userInfo._id,
        type: '1'
      }
      this.$wang.ajax({
        url: this.url.addCommentUrl,
        param
      }).then(res => {
        if (res.code === 0) {
          this.$message({
            message: 'ÁïôË®ÄÊàêÂäüÔºÅ',
            type: 'success'
          })
          this.getCommentFunc()
        } else {
          this.$message({
            message: 'ÊúçÂä°Âô®Âá∫Èîô',
            type: 'error'
          })
        }
      })
    },
    // Âà†Èô§ËØÑËÆ∫
    deleteCommmentFunc (id) {
      this.$wang.ajax({
        url: this.url.deleteCommentUrl,
        param: {
          _id: id
        }
      }).then(res => {
        if (res.code === 0) {
          this.$message({
            message: 'Âà†Èô§ËØÑËÆ∫ÊàêÂäüÔºÅ',
            type: 'success'
          })
          this.getCommentFunc()
        } else {
          this.$message({
            message: 'ÊúçÂä°Âô®Âá∫Èîô',
            type: 'error'
          })
        }
      })
    }
  },
  created () {
    this.getCommentFunc()
  }
}
</script>

